@font-face {
  font-family: 'icomoon';
  src:  url('icomoon.eot?kv60fu');
  src:  url('icomoon.eot?kv60fu#iefix') format('embedded-opentype'),
    url('icomoon.ttf?kv60fu') format('truetype'),
    url('icomoon.woff?kv60fu') format('woff'),
    url('icomoon.svg?kv60fu#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-star-full:before {
  content: "\e9d9";
}
.icon-facebook:before {
  content: "\ea90";
}
.icon-twitter:before {
  content: "\ea96";
}
.icon-linkedin2:before {
  content: "\eaca";
}
